@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap);
.section {
}

.header {
  height: 100%;
  min-height: 75vh;
  background-image: url(/static/media/home-bg.e85ef9d3.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-head {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
}

.netflix-logo {
  height: 35px;
}

.signin-btn {
  background-color: rgba(238, 70, 9, 1);
  border: none;
  outline: none;
  border-radius: 2px;
  padding: 8px 15px;
  color: white;
  font-size: 1rem;
  margin-top: 16px;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
}

.signin-btn:hover {
  background-color: rgba(238, 70, 9, 0.8);
}

.header-body {
  display: grid;
  place-content: center;
  text-align: center;
  margin-top: 5rem;
  /* margin-bottom: 14rem; */
}
.header-text {
  width: 100%;
  text-align: center;
}
.header-text > h1 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.1rem;
  width: 70%;
  margin: 0 auto;
}

.header-text > h4 {
  font-size: 1.5rem;
  margin-top: 10px;
  font-weight: 700;
}

.suggestion-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.suggestion {
  width: 95%;
  margin: 3rem auto;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1100px;
  flex-direction: row;
}
.section:nth-child(2) > .suggestion {
  flex-direction: row-reverse;
}
img {
  max-width: 100%;
  height: auto;
  margin-top: 20px;
}
.suggestion-content,
.suggestion-img {
  width: 50%;
}
.suggestion-content > h1 {
  font-size: 50px;
  margin-bottom: 8px;
}
.suggestion-content > h2 {
  font-size: 26px;
  font-weight: normal;
}

@media screen and (max-width: 1000px) {
  .suggestion {
    flex-direction: column;
  }
  .section:nth-child(2) > .suggestion {
    flex-direction: column;
  }
  .suggestion-content > h1 {
    font-size: 35px;
  }
  .suggestion-content,
  .suggestion-img {
    width: 100%;
    padding: 0 45px;
    text-align: center;
  }
}

.footer {
  width: 100%;
  color: rgba(255, 255, 255, 0.4);
}

.footer-content {
  width: 80%;
  padding: 0px 0px 2rem 0px;
  margin: 0 auto;
}
.footer-content > h2 {
  font-size: 1.1rem;
}
.link {
  font-size: 1.4rem;
}
.footer-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 20px 0px;
}

ul {
  list-style: none;
}

.list > li {
  line-height: 1.5rem;
}

a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 0.9rem;
}

a:hover {
  text-decoration: underline;
}

.accordion {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10px 0px;
}
.accordion > h3 {
  margin: 10px 0px;
  font-size: 2.5rem;
  text-align: center;
}
.accordion-info {
  width: 45%;
  margin: 10px auto;
}

.accordion-question {
  font-size: 20px;
  align-items: center;
  font-weight: normal;
  margin: 10px 0px;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.2);
  width: 100%;
  padding: 10px 20px;
  font-size: 1.5rem;
}
.accordion-icon {
  font-size: 2rem;
  cursor: pointer;
  background: transparent;
  outline: none;
  border: none;
  color: white;
}

.accordion-text {
  max-height: 1200px;
  font-size: 20px;
  font-weight: normal;
  line-height: 1.8rem;
  background: rgba(255, 255, 255, 0.3);
  padding: 0.8em 2.2em 0.8em 1.2em;
  white-space: pre-wrap;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transform: scale(1, 1);
  transform-origin: top;
  transition: all 0.4s ease-in;
}
@media screen and (max-width: 1300px) {
  .accordion-info {
    width: 60%;
  }
}

@media screen and (max-width: 600px) {
  .accordion-text {
    font-size: 16px;
    line-height: 22px;
  }
}

.accordion-email-content {
  margin: 10px auto;
  display: flex;
  flex-direction: column;
}
.accordion-email-content > h2 {
  font-size: 1rem;
  margin: 10px auto;
  text-align: center;
  font-weight: 700;
}

.email-content {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
}
.email {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.email-content > div > input {
  width: 95vw;
  max-width: 400px;
  border: none;
  padding: 10px;
  height: 60px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  font-size: 1.2rem;
}
.email > span {
  font-size: 0.8rem;
  margin-top: 10px;
  color: rgb(241, 165, 25);
}
.email-content > a:hover {
  text-decoration: none;
}
.start-btn {
  height: 100%;
  height: 60px;
  border: none;
  outline: none;
  background-color: rgba(238, 70, 9, 1);
  font-size: 20px;
  padding: 0px 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  color: white;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
}

.start-btn:hover {
  background-color: rgba(238, 70, 9, 0.9);
}

@media screen and (max-width: 900px) {
  .email-content {
    flex-direction: column;
    align-items: center;
  }
  .email-content > div > input {
    height: 50px;
    margin-bottom: 15px;
    border-radius: 5px;
  }
  .start-btn {
    height: 50px;
    padding: 0 15px;
    font-size: 15px;
    border-radius: 5px;
  }
}

.app {
  margin-bottom: 3rem;
  width: 100%;
  overflow: hidden;
}

.row {
  margin-left: 20px;
}
.row-posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 0px 20px 10px 20px;
  scroll-behavior: smooth;
  transition: all 0.4s linear;
}
.row-posters::-webkit-scrollbar {
  display: none;
}

.filter-title {
  padding: 20px 0px 0px 0px;
  letter-spacing: 0.1rem;
  word-spacing: 0.2rem;
  font-weight: bolder;
}

.row-poster {
  object-fit: contain;
  width: 100%;
  max-height: 125px;
  transition: transform 450ms;
  margin-right: 10px;
  cursor: pointer;
}

.row-posterLarge {
  max-height: 250px;
}
.row-poster:hover {
  transform: scale(1.08);
}
.row-posterLarge:hover {
  transform: scale(1.09);
}

@media screen and (max-width: 450px) {
  .row {
    margin-left: 5px;
  }
  .row-poster {
    max-height: 75px;
  }
  .row-posterLarge {
    max-height: 150px;
  }
}

.movie-content {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 10px auto;
  height: auto;
  justify-content: center;
}

.movie-info {
  margin-left: 20px;
  max-height: 250px;
  margin-bottom: -20px;
}
.movie-info > h3 {
  margin-bottom: 10px;
  font-weight: 900;
  line-height: 1.4rem;
}

.title {
  color: rgba(238, 70, 9, 0.9);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
@media screen and (max-width: 800px) {
  .movie-content {
    flex-direction: column;
  }
  .movie-info {
    margin-top: 15px;
  }
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 0px 30px 20px 30px;
  display: flex;
  justify-content: space-between;
  transition-timing-function: ease-in;
  transition: all 0.4s;
  z-index: 7654567887654567876;
}
.nav-show {
  background-color: rgba(0, 0, 0, 0.7);
}
.netflix-home-logo {
  height: 40px;
  object-fit: contain;
  z-index: 34445435767865677865787976;
}

.user-profile-image {
  height: 40px;
  object-fit: contain;
}
.search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner {
  color: white;
  object-fit: contain;
  height: 600px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.banner-content {
  padding-top: 100px;
  height: 500px;
  z-index: 345;
}

.banner-title {
  font-size: 3rem;
  font-weight: bolder;
  padding-bottom: 0.3rem;
}

.banner-description {
  width: 50rem;
  line-height: 1.5;
  padding-top: 1rem;
  font-size: 1.3rem;
  font-weight: 100;
  letter-spacing: 0.05rem;
  word-spacing: 0.1rem;
  max-width: 500px;
  color: rgba(255, 255, 255, 0.6);
}
.banner-btns {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 20px;
}

.banner-btn {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 300;
  padding: 0.5rem 1.5rem;
  margin-right: 1rem;
  font-size: 1.3rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.banner-btn:hover {
  transition: all 0.2;
}

.banner-fade {
  height: 10rem;
  width: 100vw;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  z-index: 3;
  margin-left: -30px;
  margin-bottom: -10px;
}

.movie-rating {
  display: flex;
}

.rating {
  color: rgb(12, 182, 12);
}

.year {
  margin-left: 30px;
}

.play {
  background-color: white;
  color: black;
  transition: all 0.4s linear;
}

.play:hover {
  color: white;
  background-color: gray;
}

.add {
  background-color: gray;
  color: white;
}
.add:hover {
  color: #111;
  background-color: white;
}

.icon-btn {
  display: flex;
  align-items: center;
  margin-right: 10px;
  font-size: 1.5rem;
}

@media screen and (max-width: 450px) {
  .banner-description {
    display: none;
  }
}

.signin {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/home-bg.e85ef9d3.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: rgba(255, 255, 255, 0.5);
}
/* .sign {
  min-height: 100vh;
} */

.logo {
  height: 40px;
  margin: 3rem 3rem;
}

.signin-form {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 40px 10px 100px 10px;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  margin-top: 6rem;
  margin-bottom: 5rem;
  border-radius: 5px;
}
.signin-content > h1 {
  margin-bottom: 20px;
  font-weight: 900;
}

.error-text {
  margin-bottom: 10px;
  color: white;
  font-size: 2rem;
  font-weight: bolder;
  text-align: center;
  background-color: rgb(221, 134, 19);
  border-radius: 5px;
  padding: 10px;
}
.signin-content {
  max-width: 300px;
  margin: 0 auto;
}

.signin-content > h1 {
  color: white;
}

.signin-email,
.signin-password,
.signin-username {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 20px;
}

.signin-email-input,
.signin-password-input,
.signin-username-input {
  max-width: 300px;
  width: 100%;
  height: 40px;
  padding: 0px 10px;
  border-radius: 5px;
  outline: none;
  border: none;
  margin-bottom: 5px;
  background-color: rgb(76, 71, 71);
  color: white;
}
.signin-email-input::-webkit-input-placeholder, .signin-password-input::-webkit-input-placeholder, .signin-username-input::-webkit-input-placeholder {
  color: gray;
}
.signin-email-input:-ms-input-placeholder, .signin-password-input:-ms-input-placeholder, .signin-username-input:-ms-input-placeholder {
  color: gray;
}
.signin-email-input::-ms-input-placeholder, .signin-password-input::-ms-input-placeholder, .signin-username-input::-ms-input-placeholder {
  color: gray;
}
.signin-email-input::placeholder,
.signin-password-input::placeholder,
.signin-username-input::placeholder {
  color: gray;
}

.signin-error {
  color: rgb(221, 134, 19);
  text-align: left;
  font-size: 0.8rem;
}
.alert {
  border-bottom: 3px solid rgb(221, 134, 19);
}

.signin-button {
  width: 100%;
  max-width: 300px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(238, 70, 9, 1);
  border: none;
  outline: none;
  font-size: 1.2rem;
  color: white;
  margin-bottom: 10px;
  border-radius: 7px;
  transition: all 0.4s ease-in-out;
}
.signin-button:hover {
  background-color: rgba(238, 70, 9, 0.8);
}

.checkin {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.signin-content > h4 {
  margin-bottom: 10px;
  font-size: 1rem;
}

.signin-content > h4 > a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
}

.signin-content > p {
  font-size: 0.8rem;
}
.learn {
  color: rgb(9, 98, 214);
  font-weight: bolder;
}
.footer {
  background-color: rgba(0, 0, 0, 0.6);
  padding-top: 1rem;
}

@media screen and (max-width: 400px) {
  .signin-form {
    max-width: 300px;
  }
  .signin-content {
    padding: 20px 20px;
  }
}

.profiles {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: auto;
  max-width: 80%;
}

.netflix-profile-logo {
  height: 40px;
  margin-left: 30px;
  margin-top: 30px;
}
.profile-title {
  width: 100%;
  font-size: 3rem;
  text-align: center;
  font-weight: 900;
  letter-spacing: 0.25rem;
  margin-top: 2rem;
}

.profile-name {
  color: #808080;
  text-overflow: ellipsis;
  font-size: 20px;
  font-weight: bolder;
  text-align: center;
  letter-spacing: 0.2rem;
  margin-top: 10px;
}

.profile-name:hover {
  font-weight: bolder;
  color: #e5e5e5;
  cursor: pointer;
}

.picture {
  width: 100%;
  max-width: 150px;
  height: auto;
  border: 3px solid black;
  cursor: pointer;
}

.picture:hover {
  border: 3px solid white;
}

.error-page {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  flex-direction: column;
}
.spinner {
  -webkit-animation: spin linear 1000ms infinite;
          animation: spin linear 1000ms infinite;
}
@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.error-btn {
  width: 100%;
  margin-top: 30px;
  max-width: 300px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(238, 70, 9, 1);
  border: none;
  outline: none;
  padding: 0px 10px;
  font-size: 1.2rem;
  color: white;
  margin-bottom: 10px;
  border-radius: 7px;
  transition: all 0.4s ease-in-out;
}
.error-btn:hover {
  background-color: rgba(238, 70, 9, 0.8);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", "sans-serif";
}

body {
  margin: 0;
  background-color: black;
  color: white;
}

.hr {
  height: 8px;
  background-color: rgba(255, 255, 255, 0.2);
}

